.dialog {
    width: 75%;
    border-radius: 0px 8px 8px 0px;
    display: flex;
    flex-direction: column;
    background-color: #ffff;
    box-shadow: 0px 0px 3px 0 rgba(0, 0, 0, 0.5);
}

.header {
    height: 16%;
    display: flex;
    flex-direction: column;
    box-shadow: 0px 3px 3px 0px rgba(0, 0, 0, 0.25);
    z-index: 2;
}

.fon {
    height: 25%;
    background-color: #005475;
    border-radius: 0px 8px 0px 0px;
}

.pomoshnikSearch {
    height: 45%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-left: 2%;
    padding-right: 2%;
}

.pomoshnik {
    display: flex;
    flex-direction: row;
    column-gap: 30px;
    align-items: center;
}

.iconBack:hover {
    background-color: rgb(240, 240, 240);
    border-radius: 50%;
    cursor: pointer;
}

.search {
  border-bottom: 1px solid #ccc;
}


.spanPomoshnik {
    align-self: flex-start;
    font-family: "Montserrat";
    font-size: 12px;
    position: relative;
    font-weight: 600;
}

.spanPomoshnik span {
    color: rgb(58, 58, 58);
}

.spanPomoshnik::after {
    content: 'Создание новой политики';
    position: absolute;
    left: 0;
    bottom: -15px;
    font-weight: 600;
    font-size: 12px;
    white-space: nowrap;
}

.editText {
    padding: 0.1% 2%;
    height: 40px;
    max-width: 100%;
    background-color: #ffff;
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    column-gap: 10px;
}

.imageButton {
    display: flex;
    flex-direction: row;
    column-gap: 10px;
    margin-left: auto;
}

.one {
    display: flex;
    flex-direction: row;
    cursor: pointer;
}

.one img:hover,
.two img:hover,
.four img:hover,
.three img:hover {
    background-color: rgb(240, 240, 240);
    border-radius: 20%;
}

.item {
    margin-right: 10px;
}
.itemName span {
    font-style: italic;
    font-size: 12px;
    font-family: 'Montserrat';
    font-weight: 400;
}
.select {
    position: relative;
    font-family: 'Montserrat';
}

.div {
    position: relative;
}

.div:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: rgb(240, 240, 240);
    z-index: 100;
}

.iconSavetmp:hover {
    background-color: rgb(240, 240, 240);
    border-radius: 20%;
    cursor: pointer;
}

.blockIconAdd {
    position: relative;
}

.iconAdd:hover {
    background-color: rgb(240, 240, 240);
    border-radius: 20%;
    cursor: pointer;
}

.blockIconAdd:hover::after {
    content: "Создать политику";
    white-space: nowrap;
    position: absolute;
    width: 120px;
    height: 20px;
    bottom: -120%;
    left: -170%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #3A3A3A;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
}
.blockIconSavetmp{
    position: relative;
}
.blockIconSavetmp:hover::after {
    content: "Сохранить";
    white-space: nowrap;
    position: absolute;
    width: 80px;
    height: 20px;
    bottom: -120%;
    left: -120%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #3A3A3A;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
}

.blockSelect {
    position: relative;

    & > img:hover {
        background-color: rgb(240, 240, 240);
        border-radius: 20%;
    }
}

.option {
    position: absolute;
    display: none;
    white-space: nowrap;
    right: 1%;
    background-color: #ffff;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
    cursor: pointer;
    padding: 5px;
    padding-bottom: 8px;
}

.option img {
    margin-right: 10px;
}

.option li:first-child {
    margin-top: 0px;
}

.option li {
    margin-top: 7px;
    position: relative;
    font-family: 'Montserrat';
    font-weight: 400;
    font-size: 12px;
}

.option li:hover {
    background-color: rgb(240, 240, 240)
}

.option li:before {
    content: '';
    position: absolute;
    height: 1px;
    width: 90%;
    bottom: -4px;
    right: 0;
    background-color: rgb(240, 240, 240);
}

.blockSelect:hover .option {
    display: block;
}

.two {
    display: flex;
    flex-direction: row;
    cursor: pointer;
    margin-left: 2%;
}

.three {
    display: flex;
    flex-direction: row;
    cursor: pointer;
    margin-left: 2%;
}

.four {
    display: flex;
    flex-direction: row;
    cursor: pointer;
    margin-left: 2%;
}

.five {
    height: 60%;
    width: 110px;
    display: flex;
    flex-direction: row;
    box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, 0.25);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-right: 1%;
    margin-left: 2%;
}

.sixth {
    height: 60%;
    width: 60%;
    display: flex;
    flex-direction: row;
    box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, 0.25);
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    margin-right: 1%;
    margin-left: 1%;
    column-gap: 5px;
}

.four span {
    font-weight: 600;
    font-family: 'Montserrat';
    color: rgb(58, 58, 58);
    font-size: 14px;
}


.main {
    position: relative;
    flex: 1 0 120px; 
    overflow-y: auto;
    background-color: #F0F0F0;
    padding-top: 1%;
    padding-bottom: 1%;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 1%;
}

.Teaxtaera {
    min-width: 50%;
    min-height: 10vw;
    background-color: #FFFFFF;
    padding: 1%;
    padding: 1%;
    resize: none;
}

textarea::placeholder {
    color: #B4B4B4;
    font-style: italic;
    font-size: 12px;
    font-family: 'Montserrat';
    padding-top: 3%;
    padding-left: 3%;
}

.add {
    height: 7%;
    width: 16vw;
    background-color: #ffff;
    border-radius: 10px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 1%;
    column-gap: 5%;
    box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.25);
    align-self: flex-start;
    margin-left: 25%;
}

.add div {
    color: #3A3A3A;
    font-size: 14px;
}

mark {
    background-color: lightblue;
    color: black;
}

.highlight {
    background-color: yellow;
    padding: 2px 5px;
    border-radius: 3px;
}

.load {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    top:25%;
    row-gap: 15px;
    z-index: 10000;
}

.loadImage {
    width: 33px;
    height: 33px;
    animation: rotate 1s linear infinite;
    /* Анимация вращения */
}

.spanLoad {
    font-weight: 600;
    font-family: 'Montserrat';
    color: #005475;
    font-size: 14px;
}


.error {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    top:25%;
    row-gap: 15px;
    z-index: 10000;
}


.errorImage {
    width: 33px;
    height: 33px;
    animation: error 2s ease-in-out infinite;
    /* Анимация вращения */
}

.spanError {
    font-weight: 600;
    font-family: 'Montserrat';
    color: red;
    font-size: 16px;
}

.success {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    top:25%;
    row-gap: 15px;
    z-index: 10000;
}

.successImage {
    animation: error 2s ease-in-out infinite;
}

.spanSuccess {
    font-weight: 600;
    font-family: 'Montserrat';
    color: #008000;
}

@keyframes rotate {
    from {
        transform: rotate(0deg);
        /* Начальное положение */
    }

    to {
        transform: rotate(360deg);
        /* Конечное положение */
    }
}

@keyframes error {
    0% {
        transform: scale(1);
        /* Исходный размер */
    }

    50% {
        transform: scale(1.5);
        /* Увеличение на 50% */
    }

    100% {
        transform: scale(1);
        /* Возвращение к исходному размеру */
    }
}
