.listULElement {
    display: none;
    max-height: 300px;
    min-width: 150px;
    max-width: 300px;
    overflow: auto;
    position: absolute;
    left: calc(98%);
    top: 0;
    margin: 0;
    list-style: none;
    z-index: 100;
    padding-left: 20px;

    & li {
        padding: 5px;
        cursor: pointer;
        width: 100%;
        background-color: white;

        &:hover {
            background-color: rgb(240, 240, 240)
        }
    }
}


.policySearchItem:hover .listULElement {
