    font-size: 12px;
}

@keyframes rotate {
    from {
        transform: rotate(0deg);
        /* Начальное положение */
    }

    to {
        transform: rotate(360deg);
        /* Конечное положение */
    }
}

@keyframes error {
    0% {
        transform: scale(1);
        /* Исходный размер */
    }

    50% {
        transform: scale(1.5);
        /* Увеличение на 50% */
    }

    100% {
        transform: scale(1);
        /* Возвращение к исходному размеру */
    }
}

.modal {
    position: fixed;
    z-index: 2000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.modalWindow {
    left: 10%;
    position: relative;
    height: 80vh;
    max-width: 50vw;
    background-color: #fff;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.6);
    border-radius: 10px;
    padding: 10px;
}

.modalTable {
    border-collapse: collapse;
    display: block;
}

.exitImage {
    position: absolute;
